.footer-wrapper {
  position: relative;
  margin-top: 200px;
  background-color: @basic_dark;

  &::before {
    content: "";
    position: absolute;
    bottom: 200px;
    right: 0;
    width: 100%;
    height: 1px;
    background-color: @basic-grey;

    @media (max-width: @desktop-width) {
      bottom: 150px;
    }
  }

  @media (max-width: @desktop-width) {
    margin-top: 155px;
  }

  @media (max-width: @tablet-width) {
    margin-top: 100px;
  }

  @media (max-width: @mobile-width) {
    margin-top: 70px;
  }
}
.footer-wrp {
  width: 1300px;
  height: 966px;
  margin: 0 auto;
  padding-top: 100px;

  @media (max-width: @desktop-width) {
    width: 1060px;
    height: 910px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: auto;
  }
}
.footer-inf {
  display: flex;
  justify-content: space-between;
  margin-bottom: 70px;
  width: 1300px;
  height: 136px;

  @media (max-width: @desktop-width) {
    width: 1060px;
    height: 136px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    height: 147px;
  }

  @media (max-width: @mobile-width) {
    display: block;
    width: 328px;
    height: 287px;
  }
}
.footer-title {
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 68px;
  color: @background_page;

  @media (max-width: @tablet-width) {
    width: 340px;
    font-size: 36px;
    line-height: 49px;

  }

  @media (max-width: @mobile-width) {
    display: block;
    width: 328px;
  }
}
.footer-text {
  width: 527px;
  height: 112px;
  margin-top: 18px;
  margin-right: 7px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  color: @background_page;

  @media (max-width: @desktop-width) {
    width: 420px;
    height: 88px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
    height: 110px;
    font-size: 16px;
    line-height: 140%;
    margin: 0;
    margin-top: 12px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 110px;
    margin-top: 30px;
  }
}
.form {
  display: flex;
  position: relative;
  margin-top: -5px;

  @media (max-width: @tablet-width) {
    margin-top: -16px;
  }

  @media (max-width: @mobile-width) {
    display: block;
  }
}
.form-wrapper {
  width: 770px;
  margin-top: -3px;

  @media (max-width: @desktop-width) {
    width: 630px;
  }

  @media (max-width: @tablet-width) {
    width: 360px;
  }

  // // @media (max-width: @mobile-width) {
  // //   width: 328px;
  // //   background-color: green;
  // // }
}
.form-text {
  width: 530px;
  margin-top: -3px;

  @media (max-width: @desktop-width) {
    width: 430px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
  }

  // // @media (max-width: @mobile-width) {
  // //   width: 328px;
  // //   background-color: green;
  // // }
}
.form-name,
.form-email, 
.form-country,
.form-interest,
.form-message {
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  color: @basic-grey;
  width: 640px;
  height: 50px;
  border: none;
  border-bottom: 1px solid @background_page;
  background-color: @basic_dark;
  margin-bottom: 40px;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
    width: 520px;
    margin-bottom: 38px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
.form-name::placeholder,
.form-email::placeholder,
.form-message::placeholder {
  color: @basic-grey;
}
.form-email {
  height: 50px;
  margin-bottom: 40px;

  @media (max-width: @desktop-width) {

    margin-bottom: 38px;
  }
}
.form-country {
  height: 50px;
  margin-bottom: 40px;

  @media (max-width: @desktop-width) {

    margin-bottom: 38px;
  }
}
.form-message {
  width: 529px;
  border: none;

  @media (max-width: @desktop-width) {
    width: 430px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}

.form-file {
  display: block;
  margin-top: 10px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  width: 529px;
  height: 40px;
  cursor: pointer;
  border-bottom: 1px solid @background_page;
  background-color: @basic_dark;
  color: @basic-blue;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
    width: 430px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}

.form-button {
  position: absolute;
  right: 0;
  bottom: 45px;
  width: 120px;
  height: 120px;
  border: none;
  border-radius: 50%;
  background-color: @basic-blue;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;

  &:hover {
    right: -10px;
    bottom: 10px;
    width: 150px;
    height: 150px;
  }

  &:active {
    opacity: 0;
  }

  @media (max-width: @desktop-width) {
    bottom: 35px;
  }

  @media (max-width: @mobile-width) {
    position: relative;
    margin-top: 50px;
    margin-left: 208px;
  }
}
.social {
  position: relative;
  margin-top: 110px;
  width: 1300px;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: space-between;



  @media (max-width: @desktop-width) {
    width: 1060px;
    height: 150px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    height: 150px;
  }

  @media (max-width: @mobile-width) {
    display: block;
    width: 328px;
    height: 340px;
  }
}
.social-title {
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
    color: @background_page;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @mobile-width) {
    margin-top: 50px;
    margin-bottom: 10px;
  }
  
}
.social-list {
  display: flex;
  justify-content: space-between;
  width: 972px;

  @media (max-width: @desktop-width) {
    margin-top: 5px;
    width: 788px;
  }

  @media (max-width: @tablet-width) {
    width: 427px;
  }

  @media (max-width: @mobile-width) {
    display: block;
    width: 328px;
    
  }
}
.social-item {
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
    color: @background_page;
  text-transform: uppercase;

  &:hover {
    color: @basic-blue;
    
  }

  &:active {
    opacity: 0;
  }


  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }


  @media (max-width: @mobile-width) {
    margin-top: 20px;
  }
}
.footer-privacy {
  position: relative;
  width: 1300px;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;

  @media (max-width: @desktop-width) {
    width: 1060px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
.privacy-list {
  width: 307px;
  display: flex;
  justify-content: space-between;

  @media (max-width: @desktop-width) {
    width: 265px;
  }
}
.privacy-item,
.privacy-year {
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
  text-transform: uppercase;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }
}
.footer-arrow-privacy {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 58px;
  height: 28px;
  position: absolute;
  right: -45px;
  top: -108px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  
  transform: rotate(90deg);
  writing-mode: vertical lr;
  text-orientation: upright;


}
.footer-arrow {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 58px;
  height: 28px;
  position: absolute;
  right: -95px;
  top: -75px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  
  transform: rotate(90deg);
    color: @background_page;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
    right: -70px;
    top: -70px;
  }

  @media (max-width: @tablet-width) {
    right: -40px;
  }

  @media (max-width: @mobile-width) {
    top: 0;
    right: 0;
  }
}

.footer-arrow-svg {
  transform: rotate(-90deg);
}