.outstaff-difference1 {
  position: relative;
  margin-top: 100px;
  margin-right: 35px;
  width: 495px;
  height: 450px;

  &::before {
    content: "";
    position: absolute;
    bottom: 225px;
    left: -341px;
    width: 450px;
    height: 1px;
    transform: rotate(90deg);
    background-color: @basic-grey;

    @media (max-width: @desktop-width) {
      width: 400px;
      bottom: 200px;
      left: -300px;
    }

    @media (max-width: @tablet-width) {
      display: none;
    }
  }
    
