.privacy {
  width: 1300px;
  margin: 0 auto;

  @media (max-width: @desktop-width) {
    width: 1060px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
.privacy-title {
  margin: 150px 0 50px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 68px;

  @media (max-width: @desktop-width) {
    margin: 100px 0 50px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    margin: 70px 0 50px;
    font-size: 36px;
    line-height: 49px;
  }

  @media (max-width: @mobile-width) {
    margin: 50px 0;
  }
}
.privacy-text {
  width: 1170px;
  margin-bottom: 18px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;

  @media (max-width: @desktop-width) {
    margin-bottom: 15px;
    width: 965px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {

  }
}
.privacy-subtitle {
  margin: 60px 0 30px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 49px;

  @media (max-width: @desktop-width) {
    margin: 65px 0 30px;
  }

  @media (max-width: @tablet-width) {
    font-size: 24px;
    line-height: 32px;
  }
}
.privacy-subtitle-2 {
  margin: 50px 0 30px;
}
.privacy-subtitle-3 {
  margin: 45px 0 30px;

  @media (max-width: @desktop-width) {
    margin: 45px 0 30px;
  }
}
.privacy-subtitle-4 {
  margin: 50px 0 30px;

  @media (max-width: @desktop-width) {
    margin: 50px 0 30px;
  }
} 
.privacy-subtitle-5 {
  margin: 45px 0 30px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 49px;

  @media (max-width: @tablet-width) {
    font-size: 24px;
    line-height: 32px;
  }
}
.privacy-subtitle-6 {
  margin: 50px 0 30px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 49px;

  @media (max-width: @tablet-width) {
    font-size: 24px;
    line-height: 32px;
  }
}
.privacy-subtitle-7 {
  margin: 50px 0 30px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 49px;

