.privacy-lists {
  display: block;

  // @media (max-width: @desktop-width) {
  //   margin-top: -2px;
  // }
}
.privacy-items {
  margin-left: 30px;
  width: 1300px;
  list-style: disc;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;

  @media (max-width: @desktop-width) {

    width: 965px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
.privacy-link {
  display: block;
  width: 100px;
  height: 20px;
  margin: 75px 0 95px -10px;
  text-align: right;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;

  @media (max-width: @desktop-width) {
    margin: 62px 0 69px -10px;
    font-size: 16px;
    line-height: 140%;
  }
}
.privacy-link-svg {
  margin-right: 5px;
  margin-top: 2px;
}