  justify-content: space-between;
  width: 360px;
  margin: 70px auto 30px;
    
  @media (max-width: @desktop-width) {
    width: 280px;
    margin: 50px auto 30px;
  }

  @media (max-width: @tablet-width) {
    
  }

  @media (max-width: @mobile-width) {
    width: 268px;
  }
}
.outsource-servicess-card_title {
  
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
  text-transform: uppercase;
  background: linear-gradient(89.61deg, #28C0E2 -4.38%, #212121 106.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
    
  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    
  }

  @media (max-width: @mobile-width) {
    
  }
}
.outsource-servicess-card_number {
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
    
  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    
  }

  @media (max-width: @mobile-width) {
    
  }
}
.outsource-servicess-card_text {
  width: 360px;
  margin: 0 auto;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  margin-bottom: 15px;
    
  @media (max-width: @desktop-width) {
    width: 280px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    
  }

  @media (max-width: @mobile-width) {
    width: 268px;
  }
}
.outsource-faq {
  width: 1300px;
  height: auto;
  margin: 0 auto;
  margin-top: 200px;
  
  @media (max-width: @desktop-width) {
    width: 1060px;
    margin-top: 150px;
  }

  @media (max-width: @tablet-width) {
    margin-top: 100px;
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    margin-top: 70px;
    width: 328px;
  }
}
.outsource-faq-wrapper {
  width: 1080px;
  height: auto;
  
  @media (max-width: @desktop-width) {
    width: 880px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
.outsource-faq-title {
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 68px;
  margin-bottom: 100px;
  
  @media (max-width: @desktop-width) {
    margin-bottom: 70px;
  }

  @media (max-width: @tablet-width) {
    margin-bottom: 50px;
    font-size: 36px;
    line-height: 49px;
  }
}
.outsource-faq-content {
  position: relative;
  margin-top: -1px;
  width: 1080px;
  border-top: 2px solid @basic_dark;
  background-color: @basic_white;
  
  @media (max-width: @desktop-width) {
    width: 880px;
  }

  @media (max-width: @tablet-width) {
    margin-top: -2px;
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
