.outsource-faq-text {
  margin-top: 2px;
  margin-left: 30px;
  margin-bottom: 30px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  
  @media (max-width: @desktop-width) {
    width: 800px;
    font-size: 16px;
    line-height: 140%;
    margin-left: 28px;
    margin-bottom: 19px;
  }

  @media (max-width: @tablet-width) {
    margin-left: 20px;
  }

  @media (max-width: @mobile-width) {
    width: 300px;
  }
}
.outsource-faq-item {
  margin-left: 60px;
  width: 950px;
  list-style: disc;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  
  @media (max-width: @desktop-width) {
    width: 783px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    margin-left: 40px;
    width: 600px;
  }

  @media (max-width: @mobile-width) {
    width: 280px;
  }
}
.outsource-another {
  width: 1300px;
  height: 732px;
  margin: 200px auto 0;
  
  @media (max-width: @desktop-width) {
    width: 1060px;
    height: 634px;
    margin-top: 150px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    height: 632px;
    margin-top: 100px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 1144px;
    margin-top: 70px;
  }
}
.outsource-another-title {
  width: 420px;
  height: 136px;
  margin-bottom: 100px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 68px;
  
  @media (max-width: @desktop-width) {
    width: 430px;
    height: 136px;
    margin-bottom: 65px;
  }

  @media (max-width: @tablet-width) {
    width: 300px;
    height: 98px;
    font-size: 36px;
    line-height: 49px;
    margin-bottom: 48px;
  }

  @media (max-width: @mobile-width) {
    margin-bottom: 0;
    width: 328px;
    height: 98px;
  }
}
.outsource-another-wrapper {
  display: flex;
  justify-content: space-between;
  width: 1300px;
  height: 496px;
  
  @media (max-width: @desktop-width) {
    width: 1060px;
    height: 428px;
  }

  @media (max-width: @tablet-width) {

    width: 700px;
    height: 484px;
