    width: 340px;
    height: 484px;
  }

  @media (max-width: @mobile-width) {
    margin-top: 50px;
    width: 328px;
    height: 484px;
  }
}
.outsource-another-link-img {
  width: 640px;
  height: 350px;
  background-color: @background_img;
  
