    width: 340px;
    height: 300px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 300px;
  }
}
.outsource-another-link-title {
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
