.header-arrow-blog {
  bottom: -205px;

  @media (max-width: @desktop-width) {
    bottom: -150px;
  }
}
.blog-subtitle {
  width: 1300px;
  margin: 150px auto 20px auto;
  text-align: left;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
  text-transform: uppercase;
  color: @basic-blue;

  @media (max-width: @desktop-width) {
    width: 1060px;
    margin: 100px auto 20px auto;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    margin: 70px auto 20px auto;
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    margin: 50px auto 20px auto;
    width: 328px;
  }
}
.blog-title-blue {
  color: @basic-blue;
}
.blog-title {
  width: 1300px;
  margin: 0 auto;
  margin-bottom: 150px;
  padding-right: 300px;
  font-family: "Satoshi-Black", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 70px;
  line-height: 95px;

  @media (max-width: @desktop-width) {
    width: 1060px;
    margin-bottom: 100px;
    padding-right: 250px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    font-size: 55px;
    line-height: 74px;
    padding-right: 50px;
    margin-bottom: 70px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    font-size: 45px;
    line-height: 61px;
    margin-bottom: 50px;
  }
}
.blog-categories {
  width: 1300px;
  height: 90px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid @basic-grey;
  
  @media (max-width: @desktop-width) {
    width: 1060px;
    height: 90px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    height: 70px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 50px;
  }
}
.blog-categories-list {
  margin-top: 20px;
  width: 870px;
  height: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  @media (max-width: @desktop-width) {
    margin-top: 27px;
    width: 692px;
    height: 20px;
  }

  @media (max-width: @tablet-width) {
    margin-top: 0;
    width: 572px;
    height: 24px;
  }

  // @media (max-width: @mobile-width) {
  //   width: 328px;
  // }
}

.blog-categories-item {  
  margin-top: 20px;
  height: 50px;
  font-family: "Satoshi-Medium", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  
  @media (max-width: @desktop-width) {
    margin-top: 22px;
    font-size: 14px;
    line-height: 19px;
  }

  @media (max-width: @tablet-width) {
    margin-top: 32px;
  }

  // @media (max-width: @mobile-width) {
  //   width: 328px;
  // }
}
.blog-categories-item-active {
  color: @basic-blue;
  padding-bottom: 50px;
  border-bottom: 2px solid @basic-blue;


  @media (max-width: @desktop-width) {
    margin-top: 20px;
    margin-bottom: 1px;
    height: 46px;
    padding-bottom: 0;
  }

  @media (max-width: @tablet-width) {
    margin-top: 26px;
  }
}
.blog-categories-search {
  margin-top: 15px;
  margin-right: 10px;

  @media (max-width: @desktop-width) {
    margin-top: 20px;
    margin-right: 5px;
  }

  @media (max-width: @tablet-width) {
    margin-top: 10px;
    margin-right: 8px;
  }
}
.blog-wrapper {
  position: relative;
  width: 1300px;
  height: 508px;
  margin: 0 auto;
  margin-bottom: 370px;

  @media (max-width: @desktop-width) {
    margin-bottom: 231px;
    width: 1060px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    margin-bottom: 155px;
  }
}
.blog-categories-subtitle {
  width: 1300px;
  margin: 100px auto;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 68px;
  
  @media (max-width: @desktop-width) {
    width: 1060px;
    margin: 70px auto;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    margin: 50px auto;
    font-size: 36px;
    line-height: 49px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
.blog-popular {
  position: absolute;
  display: flex;
  overflow: auto;

  @media (max-width: @desktop-width) {

  }

  @media (max-width: @tablet-width) {

  }

  @media (max-width: @mobile-width) {

  }
}
.blog-popular-card {  
  width: 420px;
  height: 508px;
  margin-right: 20px;
  
  @media (max-width: @desktop-width) {
    width: 340px;
    height: 450px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
    height: 450px;
  }

  @media (max-width: @mobile-width) {
    width: 242px;
    height: 375px;
  }
}
.blog-popular-card-date,
.blog-news-card-date {
  margin-bottom: 15px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  color: @basic-grey;
  
  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }

  // @media (max-width: @tablet-width) {
  //   width: 700px;
  // }

  @media (max-width: @mobile-width) {
    margin-bottom: 10px;
  }
}
.blog-popular-card-img {
  width: 420px;
  height: 350px;
  background-color: @background_img;
  
  @media (max-width: @desktop-width) {
    width: 340px;
    height: 300px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
    height: 300px;
  }

  @media (max-width: @mobile-width) {
    width: 242px;
    height: 250px;
  }
}
.blog-popular-card-categories,
.blog-news-card-categories  {
  width: 120px;
  margin: 15px 0;
  font-family: "Satoshi-Medium", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  background: linear-gradient(89.61deg, #28C0E2 -4.38%, #212121 106.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  
  @media (max-width: @desktop-width) {
    margin: 10px 0 15px;
    font-size: 14px;
    line-height: 19px;
  }

  // @media (max-width: @tablet-width) {
  //   width: 700px;
  // }

  @media (max-width: @mobile-width) {
    margin: 10px 0;
  }
}
.blog-popular-card-categories {
  width: 66px;
}

.blog-popular-card-title,
.blog-news-card-title {
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700; 
  font-size: 24px;
  line-height: 32px;
  
  // @media (max-width: @desktop-width) {
  //   width: 1060px;
  // }

  // @media (max-width: @tablet-width) {
  //   width: 700px;
  // }

  @media (max-width: @mobile-width) {
    font-size: 20px;
    line-height: 27px;
  }
}
.blog-news-card {
  width: 1300px;
  margin: 0 auto;
  margin-bottom: 70px;
  display: flex;
  
  @media (max-width: @desktop-width) {
    width: 1060px;
    margin-bottom: 50px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    margin-bottom: 40px;
  }

  @media (max-width: @mobile-width) {
    display: block;
    width: 328px;
    margin-bottom: 50px;
  }
}
.blog-news-card-img {
  width: 420px;
  height: 300px;
  margin-right: 20px;
  background-color: @background_img;
  
  @media (max-width: @desktop-width) {
    width: 340px;
    height: 250px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
    height: 250px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 170px;
  }
}

.blog-news-card-inner {
  width: 640px;

  @media (max-width: @desktop-width) {
    width: 520px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
  }

  // @media (max-width: @mobile-width) {
  //   width: 328px;
  // }
}
.blog-news-card-date {
  margin-top: 30px;
  
  @media (max-width: @desktop-width) {
    margin-top: 16px;
  }

  @media (max-width: @tablet-width) {
    margin-top: 40px;
  }

  @media (max-width: @mobile-width) {
    margin-top: 10px;
  }
}
.blog-news-card-text {
  margin-top: 15px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  
  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    display: none;
  }

  // @media (max-width: @mobile-width) {
  //   width: 328px;
  // }
}