.header-arrow-careers {
  bottom: -205px;

  @media (max-width: @desktop-width) {
    bottom: -150px;
  }
}
.careers-text {
  width: 1300px;
  margin: 0 auto;

  @media (max-width: @desktop-width) {
    width: 1060px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
  }
}
.careers-subtitle {
  margin: 150px auto 20px auto;
  text-align: left;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
  text-transform: uppercase;
  color: @basic-blue;

  @media (max-width: @desktop-width) {
    width: 1060px;
    margin: 100px auto 20px auto;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    margin: 70px auto 20px auto;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    margin: 50px auto 20px auto;
  }
}
.careers-title {
  width: 740px;
  margin-bottom: 150px;
  font-family: "Satoshi-Black", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 70px;
  line-height: 95px;

  @media (max-width: @desktop-width) {
    width: 740px;
    margin-bottom: 100px;
  }

  @media (max-width: @tablet-width) {
    width: 580px;
    margin-bottom: 70px;
    font-size: 55px;
    line-height: 74px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    font-size: 45px;
    line-height: 61px;
    margin-bottom: 50px;
  }
}
.careers-img {
  display: block;
  width: 1920px;
  height: 600px;
  margin: 0 auto;
  background-color: @background_img;

   @media (max-width: @desktop-width) {
    width: 1366px;
    height: 500px;
  }

  @media (max-width: @tablet-width) {
    width: 768px;
    height: 400px;
  }

  @media (max-width: @mobile-width) {
    width: 360px;
    height: 300px;
  }
}
.careers-information {
  background-color: @basic_dark;
}
.careers-information-wrapper {
  display: flex;
  justify-content: space-between;
  width: 1300px;
  height: 600px;
  margin: 0 auto;

  @media (max-width: @desktop-width) {
    width: 1060px;
    height: 500px;
  }

  @media (max-width: @tablet-width) {
    display: block;
    width: 700px;
    height: 400px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 590px;
  }
  
}
.careers-information_title {
  width: 713px;
  height: 406px;
  margin-top: 100px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 150px;
  line-height: 203px;
  color: @background_page;

  @media (max-width: @desktop-width) {
    width: 609px;
    height: 298px;
    font-size: 110px;
    line-height: 149px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    height: 95px;
    font-size: 70px;
    line-height: 95px;
    margin: 0;
    padding: 53px 0 30px 0;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    font-size: 60px;
    line-height: 81px;
  }
}
.careers-information_texts {
  margin-top: 165px;

  @media (max-width: @desktop-width) {
    margin-top: 145px;
  }

  @media (max-width: @tablet-width) {
    margin-top: 80px;
  }

}
.careers-information_text {
  width: 530px;
  height: auto;
  margin-bottom: 19px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  color: @background_page;

  @media (max-width: @desktop-width) {
    margin-bottom: 15px;
    width: 430px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {

    width: 520px;
  }

  @media (max-width: @mobile-width) {
    margin-top: 150px;
    width: 328px;
    height: 279px;
  }
}
.careers-vacancy {
  width: 1300px;
  height: 402px;
  margin: 0 auto; 
  margin-top: 194px;
  display: flex; 

  @media (max-width: @desktop-width) {
    width: 1060px;
    height: 400px;
    margin-top: 143px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    height: 352px;
    margin-top: 95px;
  }

  @media (max-width: @mobile-width) {
    display: block;
    margin-top: 70px;
    width: 328px;
    height: auto;
  }
}
.careers-vacancy-content {
  width: 440px;

   @media (max-width: @desktop-width) {
    width: 360px;
  }

  @media (max-width: @tablet-width) {
    width: 270px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 335px;
  }

}
.careers-vacancy-title {
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 68px;
  margin: 8px 0 30px 0;

    // @media (max-width: @desktop-width) {
  //   width: 1060px;
  // }

  @media (max-width: @tablet-width) {
    width: 200px;
    font-size: 36px;
    line-height: 49px;
    margin: 5px 0 20px 0;

  }

  // @media (max-width: @mobile-width) {
  //   width: 328px;
  // }
}
.careers-vacancy-text {
  width: 290px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;

   @media (max-width: @desktop-width) {
    width: 220px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    width: 230px;
  }

  // @media (max-width: @mobile-width) {
  //   width: 328px;
  // }
}
.careers-vacancy-button {
  width: 120px;
  height: 120px;
  margin-top: 50px;
  border-radius: 50%;
  border: none;
  background-color: @basic-blue;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;

  &:hover {
    right: -10px;
    bottom: 10px;
    width: 150px;
    height: 150px;
  }

  &:active {
    opacity: 0;
  }

  // @media (max-width: @desktop-width) {
  //   width: 1060px;
  // }

  @media (max-width: @tablet-width) {
    margin-top: 28px;
  }

  // @media (max-width: @mobile-width) {
  //   width: 328px;
  // }
}
.vacancy-card {
  margin-top: 5px;
  margin-right: -7px;
  align-items: flex-start;
  border-left: 1px solid @basic-grey;
}
.vacancy-card-no-current {
  width: 860px;
  height: 185px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
  text-transform: uppercase;
  border-bottom: 1px solid @basic-grey;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
    width: 700px;
  }

  @media (max-width: @tablet-width) {
    width: 430px;
    height: 150px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 300px;
  }
}
.vacancy-card-no-current-text {
  display: block;
  margin-left: 32px;
  padding-top: 125px;

  @media (max-width: @desktop-width) {
    margin-left: 27px;
    padding-top: 130px;
  }

  @media (max-width: @tablet-width) {
    padding-top: 96px;
    margin-left: 28px;
  }
}
.careers-advantages {
  width: 1300px;
  margin: 0 auto;

  @media (max-width: @desktop-width) {
    width: 1060px;
    margin-bottom: -25px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
.careers-advantages-title {
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 150px;
  line-height: 203px;
  margin: 205px 5px 70px 0;

  @media (max-width: @desktop-width) {
    width: 900px;
    margin: 155px 0 70px 0;
    font-size: 110px;
    line-height: 149px;
  }

  @media (max-width: @tablet-width) {
    margin: 100px 0 50px 0;
    width: 600px;
    font-size: 70px;
    line-height: 95px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    font-size: 60px;
    line-height: 81px;
  }
}
.careers-advantages-wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.careers-pros-card {
  width: 310px;
  height: 300px;
  margin-bottom: 20px;
  border-top: 2px solid @basic_dark;
  background-color: @basic_white;

  @media (max-width: @desktop-width) {
    margin-top: 1px;
    width: 250px;
    height: 250px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
    height: 200px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 200px;
  }
}
.careers-pros-card-none-border {
  border: none;
  background-color: @background_page;


}

.careers-pros-card-none {
  
  @media (max-width: @tablet-width) {
    display: none;
  }
}
.careers-pros-card-wrapper {
  width: 250px;
  display: flex;
  margin: 70px auto 30px;
  justify-content: space-between;

  @media (max-width: @desktop-width) {
    width: 190px;
    margin: 50px auto 30px;
  }

  @media (max-width: @tablet-width) {
    margin: 50px auto 30px;
    width: 280px;
  }

  @media (max-width: @mobile-width) {
    width: 268px;
  }
}
.careers-pros-card_title {
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
  text-transform: uppercase;
  background: linear-gradient(89.61deg, #28C0E2 -4.38%, #212121 106.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }

  // @media (max-width: @tablet-width) {
  //   width: 700px;
  // }

  // @media (max-width: @mobile-width) {
  //   width: 328px;
  // }
}
.careers-pros-card_number,
.careers-pros-card_text {
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }

  // @media (max-width: @tablet-width) {
  //   width: 700px;
  // }

  // @media (max-width: @mobile-width) {
  //   width: 328px;
  // }
}
.careers-pros-card_text {
  width: 250px;
  margin: 0 auto;

  @media (max-width: @desktop-width) {
    width: 190px;
  }

  @media (max-width: @tablet-width) {
    width: 280px;
  }

  @media (max-width: @mobile-width) {
    width: 268px;
  }
}

.careers-pros-card-img {

  @media (max-width: @desktop-width) {
    width: 250px;
    height: 250px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
    height: 200px;
  }
}